import { useMutation } from "@apollo/client"
import { Container, Typography, TextField } from "@material-ui/core"
import { useState } from "react"
import { Link } from "react-router-dom"
import { RESTORE_ACCOUNT } from "../graphql/mutations"
import Button from './Button'

const RestoreAccount = () => {
    const [mailSent, setMailSent ] = useState(false)
    const [ restoreAccount ] = useMutation( RESTORE_ACCOUNT )

    const handleResetAccount = async (e) => {
        e.preventDefault()
        try {
            await restoreAccount( { variables: { email: e.target.email.value }} )
            setMailSent(true)
        } catch (e) {
            console.log(e)
        }
        e.target.email.value = ''
    }

    if (mailSent) {
        return (
            <Container>
                <Typography variant="h4" gutterBottom>Salasana lhetetty</Typography>
                <Typography paragraph>Kertakyttinen salasana on lhetetty shkpostiisi, olettaen ett annoit
                oikean shkpostiosoitteen.</Typography>
                <Typography paragraph>Tarkista shkpostisi, sen jlkeen <Link to="/login">kirjaudu sisn</Link>.</Typography>
            </Container>
        )
    }
    return (
        <Container>
            <Typography variant="h4" gutterBottom>
                Tunnuksen palautus
            </Typography>
            <Typography paragraph>
                Kirjoita alla olevaan kenttn shkpostiosoitteesi. Kertakyttinen salasana lhetetn antamaasi shkpostiosoitteeseen.
                Mikli et koskaan liittnyt shkpostiosoitetta tunnukseesi niin... tough luck.
            </Typography>
            <form onSubmit={handleResetAccount}>
                <TextField label="Shkpostiosoite" name="email" variant="outlined" fullWidth />
                <Button type="submit">Palauta</Button>
            </form>
        </Container>
    )
}
export default RestoreAccount