import { Container, Typography, Paper, Link } from '@material-ui/core'
import useStyles from '../../hooks/useStyles'

import Aos from 'aos';
import 'aos/dist/aos.css';
import { useEffect } from 'react';

const Etusivu = () => {

    const tyylit = useStyles()

    useEffect(() => {
        Aos.init({ duration: 3000 })
    }, [])

    return (
        <Container>
            <Paper className={tyylit.paper} id="iso-naytto">
                <Typography variant="h4">
                    Iso nytt
                </Typography>
                <Typography paragraph>
                    Onpas Teill todella suuri ja mahtava nytt. Tiedoksenne ett
                    tm sovellus on suunniteltu kytettvksi knnykn pikkuruiselta nytlt.
                </Typography>
            </Paper>
            <Paper className={tyylit.paper} data-aos="fade-up">
                <Typography variant="h4">
                    Mit tm on?
                </Typography>
                <Typography paragraph>
                    Rageputt on Fullstackopen 2021 -kurssin harjoitustyn tulos. Fullstackopen on Helsingin yliopiston
                    jrjestm kurssi jossa tutustutaan JavaScriptilla tapahtuvaan moderniin websovelluskehitykseen. Ppaino on
                    React-kirjaston avulla toteutettavissa single page -sovelluksissa, ja niit tukevissa Node.js:ll toteutetuissa
                    REST-rajapinnoissa.
                </Typography>
                <Typography paragraph>
                    <Link>fullstackopen.com</Link>
                </Typography>
            </Paper>
            <Paper className={tyylit.paper} data-aos="fade-up">
                <Typography variant="h4">
                    Rageputt
                </Typography>
                <Typography paragraph>
                    Sovelluksen idea on pit kirjaa frisbeegolffin JYLY-puttipelin tuloksista. Tulokset tallentuvat palvelimelle
                    reaaliaikaisesti. Pelej voi pelata yksin tai yhdess kaverin kanssa. Jokaisella peliin osallistuvalla tulee
                    olla oma tunnus. Pelatessa porukalla tarvitsee vain yhden henkiln kirjata tuloksia sill tulokset pivittyvt
                    automaattisesti kaikille pelaajille.
                </Typography>

            </Paper>
            <Paper className={tyylit.paper} data-aos="fade-up">
                <Typography variant="h4">
                    JYLY?!?
                </Typography>
                <Typography paragraph>
                    JYLY Putting game is a putt practice drill/game created by Markus Lindqvist
                    (found by nickename JYLY in dgcr forums as well as frisbeegolf-forum). The drill
                    emphasizes putting from various distances. The more putts you sink and the further
                    you do it, more points you get. (copypaste)
                </Typography>
            </Paper>
            <Paper className={tyylit.paper} data-aos="fade-up">
                <Typography variant="h4">
                    Kerro lis
                </Typography>
                <Typography paragraph>
                    Jotta voit kytt sovellusta, tulee sinun luoda oma tunnus. Tunnuksen luomiseen riitt vain ett keksit itsellesi
                    tunnuksen ja salasanan. Voit mys antaa shkpostiosoitteesi silt varalta ett unohdat
                    salasanasi.
                </Typography>
                <Typography paragraph>
                    Tunnuksen luotuasi voit aloittaa armottoman puttaamisen. Myhemmin kun on liian pime puttaamiselle, voit ihailla ihanaa
                    diagrammidataa!
                </Typography>
                <Typography paragraph>
                    Jos satut omistamaan ystvi, voit lhett heille kaveripyynnn ja list heidt ystviksesi.
                    Tmn jlkeen voitte yhdess tuumin puttailla toisianne.
                </Typography>
            </Paper>
            <Paper className={tyylit.paper} data-aos="fade-up">
                <Typography variant="h4">
                    Vanhan pelin jatkaminen
                </Typography>
                <Typography paragraph>
                    Jos haluat jatkaa vanhaa peli, valitse 'vanhat pelit' -osiosta kyseinen peli ja paina aktivointinappia. Tmn
                    jlkeen voit jatkaa tulosten kirjaamista 'uusi peli' -sivulla.
                </Typography>
                <Typography paragraph>
                    Avoimen pelin tunnistaa oranssista kolmiosta.
                </Typography>

            </Paper>
            <Paper className={tyylit.paper} data-aos="fade-up">
                <Typography variant="h4">
                    Pelin pttminen
                </Typography>
                <Typography paragraph>
                    Kun peli on ptetty peliasetukse sivulta, tulosten kirjaaminen estetn ja tulokset otetaan huomioon statistiikka-sivulla.
                </Typography>
            </Paper>


        </Container>

    )
}
export default Etusivu
