import { Container, Typography } from "@material-ui/core";
import Page from "../../components/Page";
import PageContainer from "../../components/PageContainer";
import tuloskortti from '../../assets/tkortti.jpg';
import etaisyys from '../../assets/korti.png';
import vanhapeli from '../../assets/vanha_peli.png';

const Ohje = () => {


    return (
        <Container>
            <PageContainer>
                <Page>
                    <Typography variant="h4">Pelin valmistelu</Typography>
                    <Typography paragraph>
                    Aloita merkitsemll etisyydet metrin vlein korista vlill 5 - 10m.
                    <img src={etaisyys} alt="etaisyys" style={{ width: '100%'}} />
                    </Typography>
                </Page>
                <Page>
                    <Typography variant="h4">Uusi peli</Typography>
                    <Typography paragraph>
                        Valitse valikosta 'Uusi peli', klikkaa 'Aloita uusi peli' -nappia ja valitse mahdolliset pelaajat itsesi lisksi.
                    </Typography>
                    <Typography paragraph>
                        Peliss on 20 kierrosta. Jokaisella kierroksella puttaat 5 kertaa. Pisteit saat aina kun putti menee sisn.
                        Seuraavan kierroksen etisyys mrytyy edellisen kierroksen onnistuneiden puttien perusteella.
                    </Typography>
                </Page>
                <Page>
                    <Typography variant="h4">Tuloskortti</Typography>
                    <img src={tuloskortti} alt="kuva" style={{ width: '100%' }} />
                    <ol>
                        <li>Puttietisyys</li>
                        <li>Kierros</li>
                        <li>Pisteet</li>
                        <li>Edellinen/seuraava kierros</li>
                        <li>Kieroksen tulos/putteja sisn</li>
                    </ol>
                </Page>
                <Page>
                    <Typography variant="h4">Pelin loppu</Typography>
                    <Typography paragraph>
                        Kun 20 kierrosta on tynn, valitse pelin asetukset -vlilehdest 'Pt peli'. Peli ei voi
                        en muokata ja tulokset otetaan mukaan tilastoihin.
                    </Typography>
                </Page>
                <Page>
                    <Typography variant="h4">Vanhat pelit</Typography>
                    <Typography paragraph>
                        Vanhat pelit -osiossa voit tarkastella, aktivoida tai poistaa vanhoja pelejsi. Keskenerisen pelin
                        tunnistaa oranssista kolmiosta.
                    </Typography>
                    <img src={vanhapeli} alt="Vanha peli" style={{ width: '50%'}} />
                </Page>
            </PageContainer>
        </Container>
    )
}
export default Ohje;
